import React, { useState, useEffect } from 'react';
import './Note.css';
import AddBoxIcon from '@mui/icons-material/AddBox';
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos';
import styled from 'styled-components';
import {Folder, NoteType} from '../types'
import { fetchNotesByFolder, createNoteApi, updateNoteApi, deleteNoteApi} from '../api/noteApi'
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import LibraryAddCheckIcon from '@mui/icons-material/LibraryAddCheck';


interface DataNote {
  active: boolean;
}

const Container = styled.div<DataNote>`
  display: ${props => (props.active ? 'none' : 'flex')};
  flex-direction: column;
  align-items: center;
  width: 100%;
`;

interface NoteProps {
  folderData: Folder | undefined;
}

function Note({ folderData }: NoteProps) {
  
  //  States
  const [OpenNote, setOpenNote] = useState<boolean>(false);
  const [editNote, setEditNote] = useState<NoteType | null>(null);
  const [searchText, setSearchText] = useState<string>('');
  const [notes, setNotes] = useState<NoteType[]>([]);
  const [beforeUpdateNote, setBeforeUpdateNote] = useState<{title:string, note: string}>({title:"", note:""})
  const [statusCreateNote, setStatusCreateNote] = useState<boolean>(false) // create edit
  const [isCopying, setIsCopying] = useState<{status:boolean, index:number}>({status:false, index:-1});

  // refres Page
  useEffect(() => {
    if (!folderData?._id) return; //  folderData  _id
    fetchNotesByFolder(folderData._id)
      .then((data) => {
        setNotes(data); //  notes 
        console.log(data);
        
      })
      .catch((error) => {
        console.error('Failed to fetch notes:', error);
      });
  }, [folderData]);

  //  Functions
  const createNote = async () => {
    setStatusCreateNote(false); // reset Status

    if (!folderData) {
      alert(' Folder ');
      return;
    }

    //  
    if (!editNote?.title.trim() && !editNote?.note.trim() || !editNote) {
      // alert(' Title  Note ');
      setEditNote(null);
      setOpenNote(false); // 
      return;
    }

    try {
      const newNote = await createNoteApi(folderData._id, {
        folderId: folderData._id, //  backend 
        title: editNote.title.trim() || "",
        note: editNote.note.trim() || "",
      });

      //  state notes  ()
      // setNotes(prev => [...prev, newNote]);
      setNotes((item)=> [
        ...item, newNote
      ])
      setOpenNote(false); // 
      setEditNote(null);
    } catch (error) {
      console.error(':', error);
      alert('');
    }
  };
  const handleDelete = async (id: string) => {
    // if (!window.confirm('?')) return;
    try {
      await deleteNoteApi(id);
      //  state notes 
      setNotes(prev => prev.filter(n => n._id !== id));
    } catch (error) {
      console.error('', error);
    }
  };

  const UpdateNote = async () => {
    if (!editNote) return setOpenNote(false);

    //  note/title    API
    if (
      beforeUpdateNote.title === editNote.title &&
      beforeUpdateNote.note === editNote.note
    ) {
      return setOpenNote(false);
    }

    //  title + note    ()
    if (editNote.title.trim() === '' && editNote.note.trim() === '') {
      handleDelete(editNote._id)
      return setOpenNote(false);
    }

    try {
      const updated = await updateNoteApi(editNote._id, {
        title: editNote.title.trim(),
        note: editNote.note.trim(),
      });

      //  state notes   frontend
      setNotes((prev) =>
        prev.map((n) => (n._id === updated._id ? updated : n))
      );

      setOpenNote(false);
      setEditNote(null);
    } catch (error) {
      console.error(':', error);
    }
  };

  const filteredNotes = notes.filter(item => {
    if (searchText === "") return true;
    return (
      item.title.toLowerCase().includes(searchText.toLowerCase()) ||
      item.note.toLowerCase().includes(searchText.toLowerCase())
    );
  });

  const ItemNotes = filteredNotes.map((item, index) => (
    <div
      key={item._id}
      className='FlexItem'
      onClick={() => {
        setOpenNote(prev => !prev);
        setEditNote(item);
        setBeforeUpdateNote({title:item.title ,note:item.note})
      }}
    >
      <LibraryAddCheckIcon style={{position:"absolute",right:"20px",display: isCopying.status && isCopying.index === index ? "block": "none"}} />
      <ContentCopyIcon titleAccess='copy note' style={{position:"absolute",right:"20px",display: isCopying.status && isCopying.index === index ? "none": "block"}} 
      onClick={async(e) => {
        e.stopPropagation();  //   Event 
        await navigator.clipboard.writeText(item.note);
        setIsCopying({status:true, index:index});
        setTimeout(() => setIsCopying({status:false, index:-1}), 500); //  cursor  1 
      }}
          sx={{transition: 'color 0.3s ease, transform 0.3s ease',
        '&:hover': {
          color: 'gold',
          transform: 'scale(1.1)',
        },
      }}/>

      <h3 className='text'>{item.title}</h3>
      <p>{item.note}</p>
    </div>
  ));

  //  Render
  return (
    <>
      {/*  Note Editor */}
      <div style={{ width: '100%', display: OpenNote ? 'block' : 'none' }}>
        <div style={{ display: 'flex', cursor:'pointer' }} onClick={()=> {
          if(statusCreateNote){
            createNote();
            return;
          }
          UpdateNote();
        }}>
          <ArrowBackIosIcon
            style={{ margin: '60px 0 0 1rem', fontSize: '32px', color: 'gold' }}
          />
          <p style={{ margin: '60px 0 0 0', fontSize: '22px', color: 'gold' }}>
            
          </p>
        </div>

        <div style={{ padding: '1rem' }}>
          <input
            type='text'
            value={editNote?.title}
            onChange={e => {
              setEditNote((n) => (n ? { ...n, title: e.target.value } : n))
            }}
            placeholder='Title'
            style={{
              width: '100%',
              padding: '10px',
              fontSize: '2rem',
              borderRadius: '8px',
              border: '1px solid #333',
              marginBottom: '10px',
              background: 'black',
              color: 'white',
            }}
          />

          <textarea
            value={editNote?.note}
            onChange={e => {
              setEditNote((n) => (n ? { ...n, note: e.target.value } : n))
            }}
            placeholder='Write your note here...'
            rows={4}
            style={{
              width: '100%',
              height: '70vh',
              padding: '10px',
              fontSize: '1rem',
              borderRadius: '8px',
              border: '1px solid #333',
              resize: 'vertical',
              background: 'black',
              color: 'white',
            }}
          />
        </div>
      </div>

      {/*  Note List */}
      <Container active={OpenNote}>
        <AddBoxIcon onClick={()=> {
          setOpenNote(prev => !prev);
          setStatusCreateNote(true);
          setEditNote({
              _id: '',
              folderId: "",
              title: "",
              note: "",
              createdAt: "",
              updatedAt: "",
          })
        }}
          style={{
            textAlign: 'end',
            position: 'absolute',
            right: '20px',
            top: '10px',
            fontSize: '42px',
             cursor:'pointer',
          }}
        />

        <br />
        <br />
        <br />
        <br />

        <h1>{folderData?.name}</h1>
        <br />

        <input
          placeholder='Search'
          className='search'
          value={searchText}
          onChange={e => setSearchText(e.target.value)}
        />
        <br />

        <div className='FlexContainer'>{ItemNotes}</div>
      </Container>
    </>
  );
}

export default Note;
