import axios from '../lib/axios';  // axios  config 
import {NoteType} from '../types'

export const fetchNotesByFolder = async (folderId: string): Promise<NoteType[]> => {
  const res = await axios.get(`/appNotes/folders/${folderId}/notes`);
  return res.data;
};

// 
export const createNoteApi = async (folderId: string, data: Omit<NoteType, '_id' | 'createdAt' | 'updatedAt'>): Promise<NoteType> => {
  const res = await axios.post(`/appNotes/note`, data);
  return res.data;
};

// 
export const updateNoteApi = async (
  id: string,
  data: Partial<Omit<NoteType, '_id' | 'createdAt' | 'updatedAt'>>
): Promise<NoteType> => {
  const res = await axios.put(`/appNotes/note/${id}`, data);
  return res.data;
};

// 
export const deleteNoteApi = async (id: string) => {
  const res = await axios.delete(`/appNotes/note/${id}`);
  return res.data;
};